<template>
  <div class="register-page">
    <div class="left">
      <img src="../assets/dark.png" alt="">
      <div class="route">
        <router-link to="/login" class="btn btn-success" id="button">Login</router-link>
      </div>
    </div>
    <div class="vertical-line"></div>
    <div class="right">
      <form class="row g-3" @submit.prevent="submitForm">
        <h2 class="mb-4">Registration Form</h2>
        <div class="col-md-6">
          <label for="name" class="form-label">Name</label>
          <input v-model="name" type="text" class="form-control" id="name">
        </div>
        <div class="col-md-6">
          <label for="phoneNumber" class="form-label">Phone Number</label>
          <input v-model="phoneNumber" type="text" class="form-control" id="phoneNumber">
        </div>
        <div class="col-md-6">
          <label for="inputEmail" class="form-label">Email</label>
          <input v-model="email" type="email" class="form-control" id="inputEmail">
        </div>
        <div class="col-md-6">
          <label for="inputPassword4" class="form-label">Password</label>
          <input v-model="password" type="password" class="form-control" id="inputPassword4">
        </div>
        <div class="col-12">
          <div class="form-check form-check-inline">
            <input v-model="gender" class="form-check-input" type="radio" name="genderOptions" id="inlineRadio1" value="Male">
            <label class="form-check-label" for="inlineRadio1">Male</label>
          </div>
          <div class="form-check form-check-inline">
            <input v-model="gender" class="form-check-input" type="radio" name="genderOptions" id="inlineRadio2" value="Female">
            <label class="form-check-label" for="inlineRadio2">Female</label>
          </div>
          <div class="form-check form-check-inline">
            <input v-model="gender" class="form-check-input" type="radio" name="genderOptions" id="inlineRadio3" value="Other">
            <label class="form-check-label" for="inlineRadio3">Other</label>
          </div>
        </div>
        <div class="col-12">
          <label for="inputAddress" class="form-label">Address</label>
          <input v-model="address" type="text" class="form-control" id="inputAddress" placeholder="1234 Main St">
        </div>
        <div class="col-md-6">
          <label for="inputCity" class="form-label">City</label>
          <input v-model="city" type="text" class="form-control" id="inputCity">
        </div>
        <div class="col-md-4">
          <label for="inputState" class="form-label">State</label>
          <select v-model="state" id="inputState" class="form-select">
            <option selected>Choose...</option>
            <option>Andhra Pradesh</option>
            <option>Arunachal Pradesh</option>
            <option>Assam</option>
            <option>Bihar</option>
            <option>Chhattisgarh</option>
            <option>Goa</option>
            <option>Gujarat</option>
            <option>Haryana</option>
            <option>Himachal Pradesh</option>
            <option>Jharkhand</option>
            <option>Karnataka</option>
            <option>Kerala</option>
            <option>Madhya Pradesh</option>
            <option>Maharashtra</option>
            <option>Manipur</option>
            <option>Meghalaya</option>
            <option>Mizoram</option>
            <option>Nagaland</option>
            <option>Odisha</option>
            <option>Punjab</option>
            <option>Rajasthan</option>
            <option>Sikkim</option>
            <option>Tamil Nadu</option>
            <option>Telangana</option>
            <option>Tripura</option>
            <option>Uttar Pradesh</option>
            <option>Uttarakhand</option>
            <option>West Bengal</option>
            <option>Andaman and Nicobar Islands</option>
            <option>Chandigarh</option>
            <option>Dadra and Nagar Haveli and Daman and Diu</option>
            <option>Delhi</option>
            <option>Lakshadweep</option>
            <option>Puducherry</option>
          </select>
        </div>
        <div class="col-md-2">
          <label for="inputZip" class="form-label">Zip</label>
          <input v-model="zip" type="text" class="form-control" id="inputZip">
        </div>
        <div class="btn-group" role="group" aria-label="Basic radio toggle button group">
          <input v-model="role" type="radio" value="member" class="btn-check" name="btnradio" id="btnradio1" autocomplete="off">
          <label class="btn btn-outline-primary" for="btnradio1">MEMBER</label>

          <input v-model="role" type="radio" value="employee" class="btn-check" name="btnradio" id="btnradio2" autocomplete="off">
          <label class="btn btn-outline-success" for="btnradio2">EMPLOYEE</label>

          <input v-model="role" type="radio" value="owner" class="btn-check" name="btnradio" id="btnradio3" autocomplete="off">
          <label class="btn btn-outline-danger" for="btnradio3">OWNER</label>
        </div>
        <div class="col-12">
          <button type="submit" class="btn btn-primary">Register</button>
        </div>
      </form>
    </div>
  </div>
</template>

<style scoped>
.register-page {
  display: flex;
  align-items: center;
}

.left {
  flex: 1.5;
  height: 655px;
  background-image: url('@/assets/bg.avif');
  background-size: cover;
  background-position: center;
}

.right {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.vertical-line {
  border-left: 3px dashed rgb(0, 0, 0);
  height: 400px;
}

#button {
  height: 40px;
  width: 90px;
}

.route {
  margin-top: 20px;
}
</style>

<script>
export default {
  name: 'RegisterForm',
  data() {
    return {
      name: '',
      phoneNumber: '',
      email: '',
      password: '',
      gender: '',
      address: '',
      city: '',
      state: '',
      zip: '',
      role: '',
    };
  },
  methods: {
    submitForm() {
      const formData = {
        name: this.name,
        phoneNumber: this.phoneNumber,
        email: this.email,
        password: this.password,
        gender: this.gender,
        address: this.address,
        city: this.city,
        state: this.state,
        zip: this.zip,
        role: this.role,
      };

      fetch('http://localhost:3000/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      })
        .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          return response.json();
        })
        .then(data => {
          console.log(data);
          // Handle success, e.g., show a success message to the user

          // Clear form fields after successful registration
          this.name = '';
          this.phoneNumber = '';
          this.email = '';
          this.password = '';
          this.gender = '';
          this.address = '';
          this.city = '';
          this.state = '';
          this.zip = '';
          this.role = '';
        })
        .catch(error => {
          console.error('Error:', error.message);
          // Handle error, e.g., show an error message to the user
        });
    },
  },
};
</script>