<template>
    <div class="main">
        <h5>Name: </h5>
        <h5>Test Date: </h5>
        <h5>Name Of Test: </h5>
        <!-- Upload file -->
        <input type="file" @change="handleFileUpload" class="file-input" id="file" ref="fileInput" />
        <label for="file" class="file-label">Choose File</label>
        <!-- Display file information -->
        <p v-if="uploadedFile">Uploaded File: {{ uploadedFile.name }}</p>
    </div>
</template>

<style scoped>
@import url('../assets/UploadReport.css');
</style>

<script>
export default {
    name: 'BloodTest',
    data() {
        return {
            uploadedFile: null,
        };
    },
    methods: {
        handleFileUpload() {
            const fileInput = this.$refs.fileInput;
            const file = fileInput.files[0];

            if (file) {
                console.log('Uploaded file:', file);
                this.uploadedFile = file;
            }
        },
    },
};
</script>